import { Link } from "gatsby"
import styled from "styled-components"
import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { theme } from "../theme/GlobalStyles"

const Wrapper = styled.div`
    display: flex;
    flex-direction: row;
    @media (max-width: ${theme.mobile}) {
        flex-direction: column;
    }
`
const Container = styled.div`
  margin: 0;
  padding: 3rem 7rem;
  width: 80%;
  display: inline;
  @media (max-width: ${theme.mobile}) {
    padding: 3rem 0.5rem;
    align-self: center;
  }

`
const TabContainer = styled.div`
    display: flex;
    flex-direction: column;
    padding-left: 3rem;
    justify-content: center;
    height: 70vh;
    @media (max-width: ${theme.mobile}) {
        display: inline;
        height: auto;
        text-align: center;
        padding: 1.5rem;
    }
`
const Dot = styled.div`
    display: none;
    @media (max-width: ${theme.mobile}) {
        display: inline-block;
        height: 5px;
        width: 5px;
        background-color: ${theme.dustPink};
        border-radius: 50%;
        margin: 5px 5px 3px 5px;
        &:last-child {
            display: none;
        }
    }
`
const StyledLink = styled(Link)`
    color: ${props => props.active === props.title ? theme.dustPink : theme.lightGray};
    font-family: ${props => props.active === props.title ? "Poppins SemiBold" : "inherit"};
`

const ProjectNav = (props) => {
  const data = useStaticQuery(graphql`
    query ProjectQuery {
        site {
            siteMetadata {
                title
                projectLinks {
                    title
                    link
                }
            }
        }
    }
 `)

    const tabs = data.site.siteMetadata.projectLinks.map((projectLink, index) => (
        <>
            <StyledLink active={props.active} title={projectLink.title} to={projectLink.link} key={index}>
                {projectLink.title}
            </StyledLink>
            <Dot/>
        </>
    ));

  return (
    <Wrapper>
        <TabContainer>
            {tabs}
        </TabContainer>
        <Container>
            {props.children}
        </Container>
    </Wrapper>
  )
}

export default ProjectNav
